import React from 'react';
import photo from './../../../assets/images/photo1.webp';
import {FlexWrapper} from '../../../components/FlexWrapper';
import {Container} from '../../../components/Container';
import {Button} from '../../../components/Button';
import {S} from './Main_Styles';
import Typewriter from 'typewriter-effect';
import Tilt from 'react-parallax-tilt';



export const Main: React.FC = () => {
    return (
        <S.Main id={'main'}>
            <Container>
                <FlexWrapper align={'center'} justify={'space-around'} wrap={'wrap-reverse'}>
                    <S.InfoWrapper>
                        <div>
                            <S.Name>,   <span> </span></S.Name>
                            <S.MainTitle>Frontend </S.MainTitle>
                            <S.Description>
                                <Typewriter
                                    options={{
                                        strings: ['   2022 ,         SPA   React/Redux/TypeScript.    ,     .  ,    ,      .         , ,          .       .'],
                                        autoStart: true,
                                        loop: true,
                                        delay: 75,
                                        deleteSpeed: 25,
                                    }}
                                />
                            </S.Description>
                        </div>
                        <div>
                            <S.Links href={'https://drive.google.com/file/d/1TMVmdOT8QMqMH8ibk3ApbJ6TSmao5CKm/view?usp=sharing'} target={'_blank'}><Button> CV</Button></S.Links>
                        </div>
                    </S.InfoWrapper>

                    <Tilt>
                        <S.PhotoWrapper>
                            <S.Photo src={photo} alt="photo"/>
                        </S.PhotoWrapper>
                    </Tilt>
                </FlexWrapper>
            </Container>
        </S.Main>
    );
};